import Vue from "vue"
import Vuex from "vuex"

Vue.use(Vuex)

const store = new Vuex.Store({
	state: { // 
		userName: '', //
		password: '', //
		bottle: 0, //ESP32-CAM->SpringBootpubsub
		hasLogin: false
	},
	mutations: { // 
		login(state, name, password) { // 
			state.userName = name;
			state.password = password;
			state.hasLogin = true;
		},
		checkboottle(state, bootle_value) { //
			state.bottle = bootle_value
		},
		loginOut(state) { // 
			state.userName = '';
			state.password = '';
			state.hasLogin = false;
		}

	},
	actions: {
		init(){
			this.commit('checkboottle',0)
		},
		set_bottle(data){
			this.commit('checkboottle',data)
		}
	},
	modules: {}
})

export default store
